<template>

	<div id="chart" class="chart"></div>

</template>

<script>
	import echarts from 'echarts';

	export default {
		props:{
            step:{
                type: String,
                value: '2'
            },
        },
        data() {
        		return {
        			max: '10000',
        			min: '0'
        		}
        },
		mounted() {
			// 基于准备好的dom，初始化echarts图表
			let myChart = echarts.init(document.getElementById('chart'));

			let data = {
				value: this.step
			}
			if(this.step > 0 && this.step < 10000) {
				this.max = 10000
			} else if (this.step > 10000 && this.step < 100000) {
				this.max = 100000
			} else if (this.step > 100000 && this.step < 500000) {
				this.max = 500000
			}
			data.name = (data.value / (this.max - this.min) * 100).toFixed(2) + '%'

			let option = {
				series: [{
						type: 'gauge',
						min: this.min,
						max: this.max,
						splitNumber: 10,
						axisLine: { // 坐标轴线
							lineStyle: { // 属性lineStyle控制线条样式
								color: [
									[data.value / this.max, '#F092AE'], //根据实际数据动态改变
									[1, '#DCE0E5'],

								],
								width: 20, //半径
								shadowColor: '#fff', //默认透明
								shadowBlur: 1
							}
						},
						pointer: {
							show: false
						},
						axisLabel: {
							//show:false,
							// 坐标轴小标记
							textStyle: { // 属性lineStyle控制线条样式
								fontWeight: 'bolder',
								color: 'transparent', //刻度数字颜色 隐藏
								shadowColor: '#fff', //默认透明
								shadowBlur: 10
							}
						},
						axisTick: { // 坐标轴小标记
							length: 12, // 属性length控制线长
							lineStyle: { // 属性lineStyle控制线条样式
								color: 'transparent', //坐标轴 小刻度线颜色
								shadowColor: '#fff', //默认透明
								shadowBlur: 10
							}
						},
						splitLine: { // 分隔线
							length: 20, // 属性length控制线长
							lineStyle: { // 属性lineStyle（详见lineStyle）控制线条样式
								width: 3,
								color: 'transparent', //分割线
								shadowColor: '#fff', //默认透明
								shadowBlur: 10
							}
						},
						title: {
							show: false,
						},
						detail: {
							fontSize: 30,
							offsetCenter: [0,'-9%']
						},
						data: [
							data
						]
					}

				]
			};

			// 为echarts对象加载数据         
			myChart.setOption(option);
		}
	}
</script>

<style>
	.chart {
		height: 400px;
	}
</style>


