<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'编辑个人信息'"></mainHead>
		<!--头像-->
		<div class="center-group">
			<div class="group-con fl">
				头像
			</div>
			<div class="group-btn fr"></div>
			<input type="file" name="" id="" @change="uploadPhoto($event)" />
			<img :src='editInfImg' alt="" class="fr" />
		</div>
		<div class="inf-box">
			<!--昵称-->
			<div class="center-item">
				<div class="fl">昵称</div>
				<input class="fr arrow-right name-input" @focusout="nameChange">{{userName}}</input>
			</div>
			<!--性别-->
			<div class="center-item" @click="gender()">
				<div class="fl">性别</div>
				<p class="fr arrow-right">{{userGender}}</p>
			</div>
			<!--生日-->
			<div class="center-item" @click="goBornData()">
				<div class="fl">生日</div>
				<p class="fr arrow-right">{{bornData}}</p>
				<!--<el-date-picker  class="fr arrow-right" v-model="value1" type="date" placeholder="选择日期"></el-date-picker>-->
			</div>
		</div>
		<div class="">
			<!--手机号码-->
			<div class="center-item" @click="phone()">
				<div class="fl">手机号码</div>
				<p class="fr arrow-right">{{userPhone}}</p>
			</div>
			<!--收货地址-->
			<div class="center-item" @click="addressList()">
				<div class="fl">收货地址</div>
				<p class="fr arrow-right"></p>
			</div>
			<!--工作信息-->
			<div class="center-item" @click="goOccupation()">
				<div class="fl">职业</div>
				<p class="fr arrow-right">{{occupation}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				accessToken: '',
				userId: '',
				occupation: '',
				userPhone: '',
				bornData: '',
				userGender: '',
				userName: '',
				value1: '',
				attestFormVisible: false,
				editInfImg: 'data:image/png;base64,',
			}
		},
		methods: {
			//跳转修改昵称
			personName() {
				this.$router.push({
					path: "/name",
					query: {
						id: this.userId,
						accessToken: this.accessToken
					}
				});
			},
			//跳转修改性别
			nameChange() {
				let param = this.userId;
				axios.defaults.headers = {
					'Content-Type': "application/json",
					'authorization': "Bearer " + this.accessToken
				};
				let params = {
					name : this.userName
				}
				let _this = this;
				axios.patch(`/api/api/v1/users/` + param , (params)).then(function(res) {
					
				}).catch(error => {
					
				});	
			},
			gender() {
				this.$router.push({
					path: "/gender",
					query: {
						id: this.userId,
						accessToken: this.accessToken
					}
				});
			},
			//跳转修改生日
			goBornData() {
				this.$router.push({
					path: "/data",
					query: {
						id: this.userId,
						accessToken: this.accessToken
					}
				});
			},
			//跳转修改收货地址
			addressList() {
				this.$router.push({
					path: "/addressList",
					query: {
						id: this.userId,
						accessToken: this.accessToken
					}
				});
			},
			//跳转修改职业
			goOccupation() {
				this.$router.push({
					path: "/occupation",
					query: {
						id: this.userId,
						accessToken: this.accessToken
					}
				});
			},
			//上传头像
			uploadPhoto(e) {
				// 利用fileReader对象获取file
				var file = e.target.files[0];
				var filesize = file.size;
				var filename = file.name;
				// 2,621,440   2M
				if(filesize > 2101440) {
					// 图片大于2MB

				}
				var reader = new FileReader();
				reader.readAsDataURL(file);
				let _this = this;
				reader.onload = function(e) {
					// 读取到的图片base64 数据编码 将此编码字符串传给后台即可
					var imgcode = e.target.result;
					console.log(imgcode);
					let param = _this.userId;
					axios.defaults.headers = {
						'Content-Type': "application/json",
						'authorization': "Bearer " + _this.accessToken
					};
					let params = {
						avatar: imgcode
					}
					axios.patch(`/api/v1/users/` + param, (params)).then(function(res) {
						_this.editInfImg = res.data.avatar;
					}).catch(error => {

					});
				}
			},

		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg1";
			this.userId = this.$route.query.id;
			this.accessToken = this.$route.query.accessToken;
			let params = this.userId;
			axios.defaults.headers = {
				'Content-Type': "application/json",
				'authorization': "Bearer " + this.accessToken
			};
			let _this = this;
			axios.get(`/api/v1/users/` + params).then(function(res) {
				console.log(res)
				console.log(res.data.name)
				_this.userName = res.data.name;
				_this.userPhone = res.data.phone;
				if(res.data.gender == 'male') {
					_this.userGender = '男'
				} else {
					_this.userGender = '女'
				};
				_this.bornData = res.data.birthday;
				_this.occupation = res.data.profession;
				if(res.data.avatar != '') {
					_this.editInfImg = res.data.avatar;					
				};
			}).catch(function(error) {
				if(error.response) {
					_this.$message({
						message: error.response.data.message,
						type: 'error',
						center: true,
						duration: 1000,
						onClose: function() {
							//							window.location.reload();
						}
					});
					console.log(error.response.data);
					console.log(error.response.status);
					console.log(error.response.headers);
				} else {
					console.log('Error', error.message);
				}
				console.log(error.config);
			});
		},
		components: {
			mainHead
		}
	}
</script>

<style>
	html,
	body {
		height: 100%;
	}
	.add_bg1 {
		background: #F2F2F2;
	}
	.add_bg2 {
	    background: #fff;
	}
</style>
<style scoped>
	.center-group {
		position: relative;
		margin-top: 2px;
		padding: 0.4rem 2%;
		height: 1.6rem;
		border-bottom: 2px solid #F2F2F2;
		background: #fff;
	}
	.center-group > input {
		position: absolute;
		opacity: 0;
		padding: 0.4rem 2%;
		height: 1.6rem
	}
	.center-group img {
		width: 1.6rem;
		height: 1.6rem;
		margin-right: 0.37rem;
		border-radius: 50%;
	}
	
	.group-con {
		padding: 0.11rem 0;
		color: #333333;
		line-height: 1.6rem;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	
	.group-btn {
		width: 0.43rem;
		height: 1.6rem;
		background: url(../assets/icon_arrow.png) no-repeat right center;
		background-size: 0.43rem 0.43rem;
	}
	
	.center-item {
		margin: 1px 0;
		padding: 0 2%;
		height: 1.33rem;
		background: #fff;
		border-bottom: 2px solid #F2F2F2;
	}
	
	.center-item>div {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.33rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	
	.center-item>p {
		height: 1.33rem;
		padding-right: 0.5rem;
		background: url(../assets/icon_question_mark.png) no-repeat right center;
		background-size: 0.43rem 0.43rem;
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.33rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.center-item .name-input {
		height: 1.33rem;
		padding-right: 0.5rem;
		background: url(../assets/icon_question_mark.png) no-repeat right center;
		background-size: 0.43rem 0.43rem;
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 0.665rem;
		text-align: right;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border: none;
		outline: none;
	}
	    
	.arrow-right {
		background: url(../assets/icon_arrow.png) no-repeat right center!important;
		background-size: 0.43rem 0.43rem!important;
	}
	
	.no-arrow {
		padding: 0!important;
		background: none!important;
	}
	.inf-box {
		margin: 0.26rem 0;
	}
</style>


