import Vue from 'vue'
import Router from 'vue-router'
import task from '@/pages/task'//个人中心
import regist from '@/pages/regist'//注册
import login from '@/pages/login'//登录
import integral from '@/pages/integral'//积分
import order from '@/pages/order'//订单
import editInf from '@/pages/editInf'//编辑个人信息
import shippingAddress from '@/pages/shippingAddress'//收货地址
import addressList from '@/pages/addressList'//地址列表
import phone from '@/pages/phone'//修改电话号码
import name from '@/pages/name'//修改昵称
import gender from '@/pages/gender'//修改性别
import data from '@/pages/data'//修改日期
import occupation from '@/pages/occupation'//修改职业
import rule from '@/pages/rule'//积分规则


Vue.use(Router)

export default new Router({
	mode: 'hash',
	scrollBehavior(to, from, savedPosition) {
	 	if (savedPosition) {
	       return savedPosition
	     } else {
	       return { x: 0, y: 0 }
	     }
	},
    routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },{
      path: '/login',
      name: 'login',
      component: login
    },{
      path: '/task',
      name: 'task',
      component: task
    },{
      path: '/regist',
      name: 'regist',
      component: regist
    },{
      path: '/integral',
      name: 'integral',
      component: integral
    },{
      path: '/order',
      name: 'order',
      component: order
    },{
      path: '/editInf',
      name: 'editInf',
      component: editInf
    },{
      path: '/shippingAddress',
      name: 'shippingAddress',
      component: shippingAddress
    },{
      path: '/addressList',
      name: 'addressList',
      component: addressList
    },{
      path: '/phone',
      name: 'phone',
      component: phone
    },{
      path: '/name',
      name: 'name',
      component: name
    },{
      path: '/gender',
      name: 'gender',
      component: gender
    },{
      path: '/data',
      name: 'data',
      component: data
    },{
      path: '/occupation',
      name: 'occupation',
      component: occupation
    },{
      path: '/rule',
      name: 'rule',
      component: rule
    },{
      path: "*",
      redirect: "/"
    }
  ]
})



