<template>
	<div class="">
		<div class="">
			<!--顶部内容-->
			<mainHead :step="'会员登录'"></mainHead>
			<!--logo-->
			<div class="regist-logo"></div>
			<!--表单输入信息-->
			<div class="regist-inf">
				<div class="phone-box clear">
					<p class="fl">+86</p>
					<span class="line fl"></span>
					<input class="fl" v-model="phoneCode" type="number" pattern="\d*" placeholder="请输入手机号"/>
				</div>
				<div class="phone-box clear">
					<input class="fl" type="text" v-model="passwordCode" placeholder="请输入验证码"/>
					<div class="input-btn fr" ref='codeName'>{{codeName}}</div>
				</div>
				<div class="regist-btn" @click="login">登录</div>
				<div class="login-btn" @click="regist">注册</div>
			</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				show: false,
				userId: '',
				accessToken: '',
				phoneCode: '',
				passwordCode: '',
				codeName: '获取验证码',
			}
		},
		methods: {
			setCookie() {
				let accessToken = this.accessToken;
				let userId = this.userId;
				document.cookie="accessToken="+accessToken;
				document.cookie="userId="+userId;
		    },
			regist() {
				let params = {
					phone: this.phoneCode,
					captcha: this.passwordCode
				};
				axios.defaults.headers = {
					'Content-Type': "application/json"
				};
				let _this = this;
				axios.post(`/api/v1/auth/register` , (params)).then(function(res) {
					_this.userId = res.data.user.id;
					_this.accessToken = res.data.token.accessToken;
					_this.setCookie();
					_this.$router.push({
						path: "/task",
						query: { 
							id: _this.userId,
							accessToken: _this.accessToken
						}
					});
				}).catch(function (error) {
				    if (error.response) {
				    		if(error.response.data.message == 'Validation Error') {
				    			 _this.$message({
									message: '已注册，请直接登录',
									type: 'error',
									center: true,
									duration: 1000,
									onClose: function() {
										window.location.reload();
									}
								})
				    		} else {
				    			 _this.$message({
									message: error.response.data.message,
									type: 'error',
									center: true,
									duration: 1000,
									onClose: function() {
										window.location.reload();
									}	
				    			})
				    		}
				    } else {
				    	console.log('Error', error.message);
				    }
				    console.log(error.config);
				});
			},
			login() {
				let params = {
					phone: this.phoneCode,
					captcha: this.passwordCode
				};
				axios.defaults.headers = {
					'Content-Type': "application/json"
				};
				let _this = this;
				axios.post(`/api/v1/auth/login` , (params)).then(function(res) {
					_this.userId = res.data.user.id;
					_this.accessToken = res.data.token.accessToken;
					_this.setCookie();
					_this.$router.push({
						path: "/task",
						query: { 
							id: _this.userId,
							accessToken: _this.accessToken
						}
					});
				}).catch(function (error) {
				    if (error.response) {
				      _this.$message({
						message: error.response.data.message,
						type: 'error',
						center: true,
						duration: 1000,
						onClose: function() {
							window.location.reload();
						}
					});
				    } else {
				    	console.log('Error', error.message);
				    }
				    console.log(error.config);
				});
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg2";
			let _this = this;
			axios.defaults.headers = {
				'Content-Type': "application/json"
			};
			axios.get(`/api/v1/auth/svgcaptcha`).then(function(res) {
				_this.codeName = '';
				_this.$refs.codeName.innerHTML = res.data;
			}).catch(error => {
				
			});
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	/*logo*/
	.regist-logo {
		height: 6.5rem;
		border-top: 2px solid #F2F2F2;
		background: url(../assets/1_objects.png) no-repeat center;
		background-size: 4.83rem 1.55rem;
	}
	/*表单输入信息*/
	.regist-inf {
		width: 84%;
		margin: 0 auto;
	}
	.phone-box {
		margin-bottom: 0.44rem;
		border-bottom: 1px solid #F2F2F2;
	}
	.phone-box > p {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 1.34rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.phone-box > .line {
		width: 2px;
		height: 0.45rem;
		background: #B8B8B8;
        margin: 0.47rem 0.26rem
	}
	.phone-box > input {
		height: 1.34rem;
		border: none;
		outline: none;
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 0.67rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.input-btn {
		height: 1.34rem;
		color: #f092ae;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 1.34rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.regist-btn {
		height: 1.17rem;
		background: #f092ae;
		color: #ffffff;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.17rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border-radius: 6px;
	} 
	.login-btn {
		border: 2px solid #F092AE;
		border-radius: 6px;
		height: 1.17rem;
		margin-top: 0.5rem;
		color: #F092AE;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.17rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border-radius: 6px;
	}
	.dis-no {
		display: none;
	}
	
	.dis-show {
		display: block;
	}
	.phone-step2 {
		position: relative;
	}
	
	.step-box {
		padding-top: 200px;
		height: 200px;
		text-align: center;
	}
	
	.release-info2 p {
		margin-bottom: 4px;
		text-align: center;
	}
	
	.release-info2 p:nth-child(2) {
		color: #0092ff;
	}
	
	.ps-box {
		position: absolute;
		top: 30px;
		right: 14px;
		border-radius: 16px;
		background: #199dff;
		text-align: center;
		font-size: ;
		color: #fff;
	}
	
	.ps-box p {
		padding: 20px;
	}
	
	.jiao {
		position: absolute;
		right: 24px;
		top: -10px;
		height: 0px;
		width: 0px;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-bottom: 10px solid #199dff;
	}
</style>


