<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'添加收货地址'"></mainHead>
		<div class="shipping-box">
			<!--收货人姓名-->
			<div class="shipping-item">
				<div class="fl">收货人姓名</div>
				<input type="text" class="fr" v-model="shippingName" />
			</div>
			<!--所在地-->
			<div class="shipping-item">
				<div class="fl">所在地</div>
				<input type="text" readonly="" class="fr" v-model="shippingAddress" @click="showDistpicker" />
				<v-distpicker v-show='showChose' class='distpicker-box' type="mobile" @selected="onSelected"></v-distpicker>
			</div>
			<!--详细地址-->
			<div class="shipping-item">
				<div class="fl">详细地址</div>
				<input type="text" class="fr" v-model='shippingDel' />
			</div>
			<!--联系电话-->
			<div class="shipping-item">
				<div class="fl">联系电话</div>
				<input type="number" class="fr" v-model='shippingPhone' />
			</div>
			<!--按钮-->
			<div class="shipping-btn clear">
				<button class="fl" @click="addressDelete()">删除</button>
				<button class="fr save" @click="save()">保存</button>
			</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				showChose: false,
				shippingAddress: '',
				shippingPhone: '',
				shippingDel: '',
				shippingName: '',
				userId: '',
				accessToken: '',
				list: [],
				addressIndex: ''
			}
		},
		methods: {
			onSelected(data) {
				this.showChose = false;
				this.shippingAddress = data.province.value + data.city.value + data.area.value;
			},
			showDistpicker() {
				this.showChose = true;
			},
			addressDelete() {
				if(this.addressIndex > -1) {
					this.list.splice(this.addressIndex,1);
					let param = this.userId;
					axios.defaults.headers = {
						'Content-Type': "application/json",
						'authorization': "Bearer " + this.accessToken
					};
					let params = {
						deliveryAddress: JSON.stringify(this.list)
					};
					let _this = this;
					axios.patch(`/api/v1/users/` + param, (params)).then(function(res) {
						_this.$router.push({
							path: "/addressList",
							query: {
								id: _this.userId,
								accessToken: _this.accessToken
							}
						});
					}).catch(error => {
	
					});
				} else {
					this.shippingAddress = '';
					this.shippingPhone = '';
					this.shippingDel = '';
					this.shippingName = '';
				};
			},
			save() {
				if(this.shippingAddress == '' || this.shippingPhone == '' || this.shippingDel == '' || this.shippingName == '') {
					this.$message({
						message: '请填写必要选项',
						type: 'error',
						duration: 1000,
					});
				} else {
					let shippingJson = {
						shippingName: this.shippingName,
						shippingAddress: this.shippingAddress,
						shippingDel: this.shippingDel,
						shippingPhone: this.shippingPhone,
					};
					if(this.addressIndex > -1) {
						this.list[this.addressIndex].shippingName = this.shippingName;
						this.list[this.addressIndex].shippingAddress = this.shippingAddress;
						this.list[this.addressIndex].shippingDel = this.shippingDel;
						this.list[this.addressIndex].shippingPhone = this.shippingPhone;
					} else {
						this.list.push(shippingJson)
					}
					let param = this.userId;
					axios.defaults.headers = {
						'Content-Type': "application/json",
						'authorization': "Bearer " + this.accessToken
					};
					let params = {
						deliveryAddress: JSON.stringify(this.list)
					}
					let _this = this;
					axios.patch(`/api/v1/users/` + param, (params)).then(function(res) {
						_this.$router.push({
							path: "/addressList",
							query: {
								id: _this.userId,
								accessToken: _this.accessToken
							}
						});
					}).catch(function (error) {
					    if (error.response) {
					      _this.$message({
							message: error.response.data.message,
							type: 'error',
							center: true,
							duration: 1000,
							onClose: function() {
								window.location.reload();
							}
						});
					    } else {
					    	console.log('Error', error.message);
					    }
					    console.log(error.config);
					});
				}
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg1";
			this.userId = this.$route.query.id;
			this.accessToken = this.$route.query.accessToken;
			this.list = this.$route.query.list;
			this.addressIndex = this.$route.query.index;
			if(this.addressIndex > -1) {
				this.shippingName = this.list[this.addressIndex].shippingName;
				this.shippingAddress = this.list[this.addressIndex].shippingAddress;
				this.shippingDel = this.list[this.addressIndex].shippingDel
				this.shippingPhone = this.list[this.addressIndex].shippingPhone;
			}
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	html,
	body {
		height: 100%;
	}
	
	.add_bg3 {
		background: #F2F2F2;
	}
	
	.shipping-box {
		background: #fff;
		border-top: 2px solid #F2F2F2;
	}
	
	.shipping-item {
		border-bottom: 2px solid #F2F2F2;
		margin: 1px 0;
		padding: 0 2%;
		height: 1.33rem;
		background: #fff;
	}
	
	.shipping-item>div {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.33rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	
	.shipping-item>input {
		width: 54%;
		height: 1.33rem;
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 0.665rem;
		text-align: right;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border: none;
		outline: none;
	}
	
	.shipping-btn {
		width: 88%;
		margin: 0 auto;
		padding: 0.8rem 0;
	}
	
	.shipping-btn button {
		width: 3.86rem;
		height: 1.17rem;
		background: #fff;
		font-size: 0.43rem;
		color: #182F65;
		line-height: 0.8rem;
		border: 2px solid #182F65;
		border-radius: 6px;
		outline: none;
	}
	
	.shipping-btn button.save {
		width: 3.86rem;
		height: 1.17rem;
		background: #182F65;
		font-size: 0.43rem;
		color: #fff;
		line-height: 0.8rem;
		border: 2px solid #182F65;
		border-radius: 6px;
		outline: none;
	}
	
	.distpicker-box {
		position: fixed;
		bottom: 0;
		height: 50%;
		width: 100%;
		overflow: auto;
	}
</style>


