<template>
	<div class="center-top">
		<div class="center-icon fl" @click="routerBack">
			<i class="el-icon-arrow-left"></i>
		</div>
		<p>{{step}}</p>
		<div class="menu-box fr"></div>
	</div>
</template>

<script>
	export default {
		props:{
            step:{
                type:String,
                value:'1'
            },
            showClass: {
            	type:String
            }
        },
        methods: {
       		routerBack() {
       			this.$router.back(-1);
       		}
        }
	}
</script>

<style scoped>
	.center-top {
		position: relative;
		padding: 0 2%;
		height: 1.06rem;
		background: #fff;
	}
	.center-icon {
		width: 0.8rem;
		height: 1.06rem;
		line-height: 1.06rem;
		text-align: center;
	}
	.center-top > p {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		width: 52.8%;
		margin: auto;
		font-family: PingFangSC-Medium;
		font-size: 0.45rem;
		color: #333333;
		text-align: center;
		line-height: 1.06rem;
	}
	.menu-box {
		width: 0.8rem;
		height: 1.06rem;
		background: url(../assets/icon_more.png) center;
		background-size: 0.8rem 0.8rem;
	}
</style>


