<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'个人中心'"></mainHead>
		<!--用户头像-->
		<div class="center-group" @click="pushEditInf">
			<img :src='editInfImg' alt="" class="fl" />
			<div class="group-con fl">
				<p>用户昵称：{{taskName}}</p>
				<span>生日：{{taskData}}</span>
			</div>
			<div class="group-btn fr"></div>
			<div class="fr grade-box" :class="gradeImg">
				<div></div>
				<p>{{grade}}</p>
			</div>
		</div>
		<!--积分-->
		<div class="center-item">
			<div class="fl" @click="pushIntegral"><span>我的积分</span> {{totalScore}}</div>
			<p class="fr">获得更多积分</p>
		</div>
		<div class="center-item" @click="pushOrder">
			<div class="fl">最近订单</div>
			<p class="fr arrow-right">查看全部订单</p>
		</div>
		<div class="center-item" @click="dialogVisible = true">
			<div class="fl">积分兑换好礼</div>
			<p class="fr arrow-right"></p>
		</div>
		<div class="center-item">
			<div class="fl">客服电话</div>
			<p class="fr no-arrow">400-1887-354</p>
		</div>
		<div class="v-modal" style="z-index: 2000;" v-show="dialogVisible" @click="dialogVisible = false">
		</div>
		<div class="gift-box" v-show="dialogVisible">好礼正在筹备中~~~</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				dialogVisible: false,
				totalScore: '',
				gradeImg: '',
				userId: '',
				grade: '黄金会员',
				taskName: '',
				taskData: '',
				editInfImg: 'data:image/png;base64,',
			}
		},
		methods: {
			//跳转编辑个人信息页面
			pushEditInf() {
				this.$router.push({
					path: "/editInf",
					query: {
						id: this.userId,
						accessToken: this.accessToken
					}
				});
			},
			//跳转我的积分
			pushIntegral() {
				this.$router.push({
					path: "/integral",
					query: {
						id: this.userId,
						accessToken: this.accessToken,
						totalScore: this.totalScore
					}
				});
			},
			//跳转我的订单
			pushOrder() {
				this.$router.push({
					path: "/order",
					query: {

					}
				});
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg1";
			this.userId = this.$route.query.id;
			this.accessToken = this.$route.query.accessToken;
			let params = this.$route.query.id;
			axios.defaults.headers = {
				'Content-Type': "application/json",
				'authorization': "Bearer " + this.accessToken
			};
			let _this = this;
			axios.get(`/api/v1/users/` + params).then(function(res) {
				console.log(res)
				console.log(res.data)
				_this.taskName = res.data.name;
				_this.taskData = res.data.birthday;
				if(res.data.avatar != '') {
					_this.editInfImg = res.data.avatar;
				};
				_this.totalScore = res.data.totalScore;
				if(_this.totalScore > 0 && _this.totalScore < 10000) {
					_this.gradeImg = 'grade-img2';
					_this.grade = 'VIP会员';
				} else if(_this.totalScore > 10000 && _this.totalScore < 100000) {
					_this.gradeImg = 'grade-img3';
					_this.grade = '白银会员';
				} else if(_this.totalScore > 100000 && _this.totalScore < 500000) {
					_this.gradeImg = 'grade-img1';
					_this.grade = '黄金会员';
				} else if(_this.totalScore > 500000) {
					_this.gradeImg = 'grade-img4';
					_this.grade = '钻石会员';
				}
			}).catch(function(error) {
				if(error.response) {
					_this.$message({
						message: error.response.data.message,
						type: 'error',
						center: true,
						duration: 1000,
						onClose: function() {
							//							window.location.reload();
						}
					});
					console.log(error.response.data);
					console.log(error.response.status);
					console.log(error.response.headers);
				} else {
					console.log('Error', error.message);
				}
				console.log(error.config);
			});
		},
		components: {
			mainHead
		}
	}
</script>
<style type="text/css">
	html,
	body {
		height: 100%;
	}
	
	.add_bg1 {
		background: #F2F2F2;
	}
</style>
