		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 20px;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	
	.gift-box {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
		width: 200px;
		height: 200px;
		background: #fff;
		font-size: 0.5rem;
		color: #000;
		line-height: 200px;
		text-align: center;
		border-radius: 8px;
		z-index: 20001;
	}
</style>


