<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'日期选择'"></mainHead>
		<div class="phone-box">
			<!--日期选择-->
			<div class="phone-item">
				<el-date-picker class="arrow-right" @change="chooseTimeRange" v-model="value1" type="date" placeholder="选择日期"></el-date-picker>
			</div>
			<!--按钮-->
			<div class="phone-btn clear">
				<button class="" @click="change">保存</button>
			</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				accessToken: '',
				userId: '',
				radio: '1',
				value1: '',
				bornData: ''
			}
		},
		methods: {
			chooseTimeRange(t) {
		    	this.bornData = t;
		    },
			change() {	
				if(this.bornData == '') {
					this.$message({
			          	message: '请选择日期',
			          	type: 'error',
			          	duration: 1000,
			        });
				} else {
					let param = this.userId;
					axios.defaults.headers = {
						'Content-Type': "application/json",
						'authorization': "Bearer " + this.accessToken
					};
					//male female
					let params = {
						birthday : this.bornData
					}
					let _this = this;
					axios.patch(`/api/v1/users/` + param , (params)).then(function(res) {
						_this.$message({
				          	message: '修改成功',
				          	type: 'success',
				          	duration: 1000,
				          	onClose: function() {
				          		_this.$router.push({
									path: "/editInf",
									query: {
										id: _this.userId,
										accessToken: _this.accessToken
									}
								});
				          	}
				        });
					}).catch(error => {
						
					});			
				}
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg2";
			this.userId = this.$route.query.id;
			this.accessToken = this.$route.query.accessToken;
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	html,body {
		height: 100%;
	}
	.add_bg3 {
	    background: #F2F2F2;
	}
	.phone-box {
		padding: 0 2%;
		background: #fff;
	}
	.phone-btn {
		width: 88%;
		margin: 0 auto;
		padding: 0.8rem 0;
	}
	.phone-item {
		text-align: center;
		line-height: 1rem;
	}
	.phone-item > input {
		width: 100%;
		height: 1.33rem;
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.33rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border: none;
		outline: none;
	}
	.phone-btn button {
		width: 100%;
		height: 1.17rem;
		background: #182F65;
		font-size: 0.43rem;
		color: #fff;
		line-height: 0.8rem;
		border: 2px solid #182F65;
		border-radius: 6px;
		outline: none;
	}
</style>


