<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'我的订单'"></mainHead>
		<div class="order-box">
			<div></div>
			<p>您目前没有任何订单</p>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg2";
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	.order-box {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		margin: auto;
		width: 4rem;
		height: 5rem;
	}
	.order-box > div {
		width: 4rem;
		height: 4rem;
		background: url(../assets/1_objects_1.png) no-repeat center;
		background-size: contain;
	}
	.order-box > p {
		color: #f093af;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
</style>


