<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'我的积分'"></mainHead>
		<!--积分进度图-->
		<div class="integral-pic">
			<span class="small-num">{{min}}</span>
			<ring :step="totalScore"></ring>
			<span class="big-num">{{max}}</span>
			<div class="rule-box" @click="goRule()"><span>积分规则</span></div>
		</div>
		<!--积分详情-->
		<div class="integral-det">
			<div class="integral-tit">积分记录</div>
			<ul class="integral-box">
				<li class="clear" v-for="con in lists">
					<span class="fl">{{con.sourceName}}</span>
					<div class="fr">
						<p>+{{con.value}}</p>
						<span>{{count(con.updatedAt)}}</span>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	import ring from '@/components/ring';
	export default {
		data() {
			return {
				totalScore: '',
				userId: '',
				accessToken: '',
				lists: [],
				max: '',
				min: 0
			}
		},
		methods: {
			count(e) {
				return new Date(e).toLocaleDateString()
			},
			goRule() {
				this.$router.push({
					path: "/rule",
					query: {
						
					}
				});
			}
		},
		created() {
			this.totalScore = this.$route.query.totalScore;
			if(this.totalScore > 0 && this.totalScore < 10000) {
				this.max = 10000
			} else if (this.totalScore > 10000 && this.totalScore < 100000) {
				this.max = 100000
			} else if (this.totalScore > 100000 && this.totalScore < 500000) {
				this.max = 500000
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg2";
			this.userId = this.$route.query.id;
			this.accessToken = this.$route.query.accessToken;
			let params = this.userId;
			let _this = this;
			axios.defaults.headers = {
				'Content-Type': "application/json",
				'authorization': "Bearer " + this.accessToken
			};
			axios.get(`/api/v1/users/` + params + '/score').then(function(res) {
				console.log(res.data);
				_this.lists = res.data.score;
			}).catch(error => {
				
			});
		},
		components: {
			mainHead,
			ring
		}
	}
</script>

<style scoped> 
	html,body {
		height: 100%;
	}
	.add_bg2 {
	    background: #fff;
	}
	.integral-pic {
		position: relative;
		border-top: 1px solid #F2F2F2;
		border-bottom: 20px solid #F2F2F2;
	}
	.integral-tit {
		padding: 0 2%;
		height: 2.21rem;
		border-bottom: 1px solid #F2F2F2;
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 2.21rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.integral-box li {
		padding: 0 2%;
		height: 2.21rem;
		border-bottom: 1px solid #F2F2F2;
	}
	.integral-box li > span {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		line-height: 1rem;
	}
	.integral-box li > div > p {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		text-align: right;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		line-height: 1rem;
	}
	.integral-box li > div > span {
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		text-align: right;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.small-num {
		position: absolute;
		left: 16%;
    		bottom: 86px;
	}
	.big-num {
		position: absolute;
		right: 8%;
    		bottom: 86px;
	}
	.rule-box {
		position: absolute;
		bottom: 48px;
		width: 100%;
		font-family: PingFangSC-Regular;
		font-size: 0.37rem;
		color: #939393;
		line-height: 0.5rem;
		text-align: center;
	}
	.rule-box > span {
		display: inline-block;
		padding-right: 0.5rem;
		background: url(../assets/icon_question_mark.png) no-repeat right center;
		background-size: 0.42rem 0.42rem;
	}
</style>


