<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'修改电话号码'"></mainHead>
		<div class="phone-box">
			<!--地址列表-->
			<div class="phone-item">
				<input class="fl" type="number" pattern="\d*" placeholder="请输入手机号"/>
			</div>
			<!--按钮-->
			<div class="phone-btn clear">
				<button class="">保存</button>
			</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg3";
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	html,body {
		height: 100%;
	}
	.add_bg3 {
	    background: #F2F2F2;
	}
	.phone-box {
		padding: 0 2%;
		background: #fff;
	}
	.phone-btn {
		width: 88%;
		margin: 0 auto;
		padding: 0.8rem 0;
	}
	.phone-item > input {
		width: 100%;
		height: 1.33rem;
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.33rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border: none;
		outline: none;
	}
	.phone-btn button {
		width: 100%;
		height: 1.17rem;
		background: #182F65;
		font-size: 0.43rem;
		color: #fff;
		line-height: 0.8rem;
		border: 2px solid #182F65;
		border-radius: 6px;
		outline: none;
	}
</style>


