<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'收货地址'"></mainHead>
		<div class="shipping-box">
			<!--地址列表-->
			<div class="shipping-item" v-for="(list,index) in addressList" @click="changeAddress(index)">
				<div class="fl">
					<p>收货人姓名: {{list.shippingName}}</p>
					<p>{{list.shippingAddress}}</p>
					<p>{{list.shippingDel}}</p>
					<p>{{list.shippingPhone}}</p>
				</div>
				 <p class="fr arrow-right"></p>
			</div>
			<!--按钮-->
			<div class="shipping-btn clear">
				<button class="" @click="goAddress">添加新地址</button>
			</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				accessToken: '',
				userId: '',
				addressList: [],
			}
		},
		methods: {
			changeAddress(index) {
				this.$router.push({
					path: "/shippingAddress",
					query: {
						id: this.userId,
						accessToken: this.accessToken,
						list: this.addressList,
						index: index
					}
				});
			},
			goAddress() {
				this.$router.push({
					path: "/shippingAddress",
					query: {
						id: this.userId,
						accessToken: this.accessToken,
						list: this.addressList,
						index: -1
					}
				});
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg1";
			this.userId = this.$route.query.id;
			this.accessToken = this.$route.query.accessToken;
			let params = this.userId;
			axios.defaults.headers = {
				'Content-Type': "application/json",
				'authorization': "Bearer " + this.accessToken
			};
			let _this = this;
			axios.get(`/api/v1/users/` + params).then(function(res) {
				_this.addressList = JSON.parse(res.data.deliveryAddress);
				console.log(_this.addressList)
			}).catch(function(error) {
				if(error.response) {
					_this.$message({
						message: error.response.data.message,
						type: 'error',
			          	duration: 1000,
						center: true,
						onClose: function() {
							//							window.location.reload();
						}
					});
					console.log(error.response.data);
					console.log(error.response.status);
					console.log(error.response.headers);
				} else {
					console.log('Error', error.message);
				}
				console.log(error.config);
			});
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	html,body {
		height: 100%;
	}
	.add_bg3 {
	    background: #F2F2F2;
	}
	.shipping-box {
		background: #fff;
	}
	.shipping-item {
		margin: 1px 0;
		padding: 0 2%;
		height: 3.2rem;
		background: #fff;
		border-bottom: 1px solid #ccc;
	}
	.shipping-item > div > p {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		line-height: 0.8rem;
	}
	.shipping-item > p {
		height: 3.2rem;
		padding-right: 0.5rem;
		background: url(../assets/icon_arrow.png) no-repeat right center;
		background-size: 0.43rem 0.43rem;
		color: #939393;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.33rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.shipping-btn {
		width: 88%;
		margin: 0 auto;
		padding: 0.8rem 0;
	}
	.shipping-btn button {
		width: 100%;
		height: 1.17rem;
		background: #182F65;
		font-size: 0.43rem;
		color: #fff;
		line-height: 0.8rem;
		border: 2px solid #182F65;
		border-radius: 6px;
		outline: none;
	}
</style>


