<template>
	<div class="">
		<!--顶部内容-->
		<mainHead :step="'会员注册'"></mainHead>
		<!--logo-->
		<div class="regist-logo"></div>
		<!--表单输入信息-->
		<div class="regist-inf">
			<div class="phone-box clear">
				<p class="fl">+86</p>
				<span class="line fl"></span>
				<input class="fl" v-model="phoneCode" type="number" pattern="\d*" placeholder="请输入手机号"/>
			</div>
			<div class="phone-box clear">
				<input class="fl" type="text" v-model="passwordCode" placeholder="请输入验证码"/>
				<div class="input-btn fr" ref='codeName'>{{codeName}}</div>
			</div>
			<div class="login-btn" @click="login">注册</div>
		</div>
	</div>
</template>

<script>
	import mainHead from '@/components/common-top';
	export default {
		data() {
			return {
				userId: '',
				accessToken: '',
				phoneCode: '',
				passwordCode: '',
				codeName: '获取验证码',
			}
		},
		methods: {
			login() {
				let params = {
					phone: this.phoneCode,
					captcha: this.passwordCode
				};
				axios.defaults.headers = {
					'Content-Type': "application/json"
				};
				let _this = this;
				axios.post(`/api/v1/auth/register` , (params)).then(function(res) {
					console.log(res)
					console.log(res.data.user.id)
					console.log(res.data.token.accessToken)
					_this.userId = res.data.user.id;
					_this.accessToken = res.data.token.accessToken;
					_this.$router.push({
						path: "/task",
						query: { 
							id: _this.userId,
							accessToken: _this.accessToken
						}
					});
				}).catch(function (error) {
				    if (error.response) {
				      _this.$message({
						message: error.response.data.message,
						type: 'error',
						center: true,
						onClose: function() {
							window.location.reload();
						}
					});
				    console.log(error.response.data);
				    console.log(error.response.status);
				    console.log(error.response.headers);
				    } else {
				    	console.log('Error', error.message);
				    }
				    console.log(error.config);
				    
				});
			}
		},
		mounted() {
			document.getElementsByTagName("body")[0].className = "add_bg2";
			let _this = this;
			axios.defaults.headers = {
				'Content-Type': "application/json"
			};
			axios.get(`/api/v1/auth/svgcaptcha`).then(function(res) {
				_this.codeName = '';
				_this.$refs.codeName.innerHTML = res.data;
			}).catch(error => {
				
			});
		},
		components: {
			mainHead
		}
	}
</script>

<style scoped>
	html,body {
		height: 100%;
	}
	.add_bg2 {
	    background: #fff;
	}
	/*logo*/
	.regist-logo {
		height: 6.5rem;
		border-top: 2px solid #F2F2F2;
		background: url(../assets/1_objects.png) no-repeat center;
		background-size: 4.83rem 1.55rem;
	}
	/*表单输入信息*/
	.regist-inf {
		width: 84%;
		margin: 0 auto;
	}
	.phone-box {
		margin-bottom: 0.44rem;
		border-bottom: 1px solid #F2F2F2;
	}
	.phone-box > p {
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 1.34rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.phone-box > .line {
		width: 2px;
		height: 0.45rem;
		background: #B8B8B8;
        margin: 0.47rem 0.26rem
	}
	.phone-box > input {
		height: 1.34rem;
		border: none;
		outline: none;
		color: #333333;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 0.67rem;
		text-align: left;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.input-btn {
		height: 1.34rem;
		color: #f092ae;
		font-family: PingFangSC;
		font-size: 0.37rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 400;
		line-height: 1.34rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
	}
	.regist-btn {
		height: 1.17rem;
		background: #f092ae;
		color: #ffffff;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.17rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border-radius: 6px;
	} 
	.login-btn {
		height: 1.17rem;
		margin-top: 0.5rem;
		background: #182F65;
		color: #ffffff;
		font-family: PingFangSC;
		font-size: 0.43rem;
		font-style: normal;
		font-stretch: normal;
		font-weight: 500;
		line-height: 1.17rem;
		text-align: center;
		text-transform: none;
		text-decoration: none;
		letter-spacing: 0px;
		border-radius: 6px;
	}
</style>


